import { useState } from "react";
import imageCompression from "browser-image-compression";

//no use of this becuase it cannot handle the URL for image
//that's what i think so
import FileSaver, { saveAs } from 'file-saver';

export default function App() {
    //state for storing the original Image
  const [originalFile, setOriginalFile] = useState(null);
    //state for storing the CompreeImage
  const [compressedFile, setCompressedFile] = useState(null);
    //state for storing the downloadFileLink so we can use it in href in 'a' tag
  const [downloadFileLink, setdownloadFileLink] = useState(null);
    //state for storing the downloadFileName becuase it's needed
  const [downloadFileName, setdownloadFileName] = useState(null);

    //it will handle the compresson of the originalFile
  const handleFileChange = (event) => {
      //get the image from the uploaded by the user and store it in originalFile
    setOriginalFile(event.target.files[0]);
  };

  const handleCompress = async () => {
    try {
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 800,
      };

        //store the  compressedImage in a variable compressedImage
      const compressedImage = await new imageCompression(originalFile, options);

        //set the compressedFile by using the above vairable
      setCompressedFile(compressedImage);
        //set the image url for the href/src for 'a' tag or 'src' in html
        //the compressedFile contains the blob but we need a url to store it in 'a'/'src' in html so we convert the blob into DOMString representation
        setdownloadFileLink(URL.createObjectURL(compressedImage));
        //set the downloadFileName
        setdownloadFileName(compressedImage.name);
    } catch (error) {
      console.error("Image compression error:", error);
    }
  };

    //not in use anymore
const saveFile =()=>{
FileSaver.saveAs(
process.env.REACT_APP_CLIENT_URL+ URL.createObjectURL(compressedFile) ,"image.png"
);
}


  return (
    <>
      <div className="text-center text-4xl font-bold my-5">React Image Compressor</div>
      <div className="container mx-auto px-5 my-5 ">
      <div className="grid grid-cols-1  gap-4 justify-items-center rounded-lg  p-4 ">
        <div className="flex flex-col items-center rounded-lg border-4 border-amber-950">
          <div className="text-3xl my-5 mx-4 ">Image Compressor</div>
      {/* we have used a ternary operator below to display image */}
      {downloadFileLink ? <img
             className="h-48 w-5/6"
            // src={process.env.PUBLIC_URL + "/images/Upload.png"}
     src= {downloadFileLink}

          />
      :<img className="w-1/2 " src={process.env.PUBLIC_URL + "/images/Upload.png"}/>}
      <div className="card card-compact w-96 bg-base-100 shadow-xl">
  <div className="card-body">
    <h2 className="card-title">Upload</h2>
            <input
              className="my-3 px-3 py-2 btn btn-primary"
              type="file"
              onChange={handleFileChange}
            />
      <div className="flex flex-row justify-center">
            <button
              className=" btn btn-primaryborder-4 border-black h-10 mx-3 px-3 w-max"
              onClick={handleCompress}
            >
              Compress Image

            </button>
  </div>
  </div>
      <div className="flex flex-row justify-center mb-9">
      {/* create a download link of the given link in href */}
      <a className="border border-black p-3 rounded-lg hover:bg-teal-50" href={downloadFileLink} download={downloadFileName}  >Download
</a>
</div>
        </div>
        </div>
      </div>
      </div>
    </>
  );
}
